.feed {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.order {
  border-radius: 40px;
  background-color: #1c1c21;
  display: flex;
  flex-direction: column;
  gap: 24px;
  align-items: center;
  box-sizing: border-box;
  padding: 24px;
}

.details {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 24px;
}

.order_title {
  width: 100%;
  text-align: left;
}

.order_summary {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
}

.ingrediences {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.ingredient {
  width: 64px;
  height: 64px;
  border-radius: 100px;
  border: 2px solid #801AB2;
  background: #131316;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 115px 64px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ingredient:not(:first-child) {
  margin-left: -20px;
}

.ingredient_counter {
  composes: ingredient;
}

.ingredient-counter::after {
  content: "";
  display: block;
  width: 64px;
  height: 64px;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: #1C1C21;
  opacity: .6;
}

.ingredient_counter {
  z-index: 11;
}

.price {
  display: flex;
  gap: 8px;
  align-items: center;
}

a {
  all: unset;
}

.status {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 8px;
}