.modal_container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.modal {
  z-index: 2;
  background-color: #1C1C21;
  width: 720px;
  border-radius: 40px;
  border: 1px solid  rgba(76, 76, 255, 0.20);
  position: relative;
}

.button_close {
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: block;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 40px;
  top: 60px;
  transition: opacity .3s;
  z-index: 2;
  transform: translate(-50%, -50%);
}

.button_close:hover {
  opacity: .6;
}