.section {
  min-width: 576px;
}

.constructor {
  min-height: 624px;
}

.ingredients {
  display: flex;
  flex-direction: column;
  gap: 16px;
  list-style-type: none;
  padding: 0;
  margin-top: 16px;
  margin-bottom: 16px;
  max-height: 464px;
  overflow: auto;
  overflow-x: hidden;
}

.order {
  display: flex;
  gap: 40px;
  justify-content: flex-end;
  align-items: center;
}

.price {
  display: flex;
  gap: 8px;
}